<template>
    <div class="wrap">
        <div class="main">
            <div class="title">百度智能小程序调起协议 && Web 化预览工具</div>

            <vue-form-generator :schema="schema" :model="model" :options="formOptions"></vue-form-generator>

            <div class="result">
                <qrcode-vue class="qrcode" :value="result" :size="size" level="H"></qrcode-vue>
                <textarea class="text" :value="result"></textarea>
            </div>

            <div class="foot">
                <p>Tips:</p>
                <p>1、<span class="button offline" @click="getBundleId">线下版本预览请点击这里</span> <span v-if="bundleInfo">{{bundleInfo}}</span></p>
                <p>2、<span class="button" v-tap="{methods: preview}">Web 化预览请点击这里</span>（若 Web 化版本已经上线）</p>
                <p>3、如何使用特定的场景值打开开发者工具生成的预览版小程序：① 将 App Key 填写为 ${appKey}_${suffix} 或 ${bundleId}。比如 <span>4fecoAqgCIUtzIyA4FAPgoyrc4oUc25c_dev18505。</span>appKey 可在 <span>小程序平台 => 设置 => <a href="https://smartprogram.baidu.com/mappconsole/main/set" target="_blank">开发设置</a></span> 中查询，suffix 或 bundleId 可在开发者工具预览生成的二维码上查看。② 填写场景值后，<span><a href="https://smartprogram.baidu.com/docs/data/scene/" target="_blank">（点击查看场景值文档）</a></span>使用百度 App 扫描生成的二维码。</p>
            </div>
        </div>
        <div class="preview">
            <iframe :src="weburl" frameborder="0" name="preview" id="webframe"></iframe>
        </div>
    </div>
</template>
<script>
    import style from '../style/style.less';
    import QrcodeVue from 'qrcode.vue';
    import VueFormGenerator from "vue-form-generator";
    import axios from 'axios';
    export default {
        components: {
            QrcodeVue
        },
        data() {
            return {
                bundleInfo: '',
                model: {
                    appKey: '4fecoAqgCIUtzIyA4FAPgoyrc4oUc25c',
                    path: '',
                    query: '',
                    source: ''
                },
                schema: {
                    fields: [{
                        type: 'input',
                        inputType: 'text',
                        label: 'App Key',
                        model: 'appKey',
                        placeholder: '可在 小程序平台 => 设置 => 开发设置 中查询',
                        required: true
                    }, {
                        type: 'input',
                        inputType: 'text',
                        label: '启动页面',
                        model: 'path',
                        placeholder: '默认为首页。如：pages/index/index'
                    }, {
                        type: 'input',
                        inputType: 'text',
                        label: '启动参数',
                        model: 'query',
                        placeholder: '可以在 Page 的 onLoad  中获取。如：key=encodeURIComponent(value)'
                    }, {
                        type: 'input',
                        inputType: 'text',
                        label: '进入场景',
                        model: 'source',
                        required: true,
                        placeholder: '可以在 App 的 onLaunch 和 onShow 中获取。'
                    }]
                },
                formOptions: {
                    validateAfterChanged: true
                },
                weburl: ''
            }
        },
        computed: {
            size() {
                let screenWidth = document.documentElement.clientWidth;
                return screenWidth < 925 ? (screenWidth <= 320 ? 120 : 150) : 180;
            },
            result() {
                let {
                    appKey,
                    path,
                    query,
                    source
                } = this.model;

                if (path.match(/^\//g)) {
                    path = path.slice(1);
                }

                if (path.match(/\/$/g)) {
                    path = path.slice(0, -1);
                }

                let infoList = ['swan'];
                appKey && (appKey = appKey.trim());

                if (!/_dev\w+/.test(appKey)) {
                    this.bundleInfo = '';
                }
                appKey && infoList.push(appKey);
                path && infoList.push(path);

                // query 参数
                let queryObj = {
                    _baiduboxapp: encodeURIComponent(JSON.stringify({
                        from: source,
                        ext: {}
                    })),
                    callback: '_bdbox_js_275',
                    upgrade: 0
                };
                let innerQuery = this.paramsJoin(queryObj);
                // encodeURIComponent
                let queryStr = '?' + (query ? `${query}&` : '') + innerQuery;

                infoList.push(queryStr);
                let infoStr = infoList.join('/');
                return `baiduboxapp://${infoStr}`;
            },
            isDev() {
                return this
            }
        },
        methods: {
            getBundleId() {
                let self = this;
                let {
                    appKey
                } = self.model;
                if (/_dev\w+/.test(appKey)) {
                    self.bundleInfo = 'Waiting...正在请求 bundleId';
                    axios.get(`${location.protocol}//smartprogram.baidu.com/mappconsole/api/getScheme?key=${appKey}`)
                        .then(function(response) {
                            if (response.data.data && response.data.data.bundleId) {
                                self.model.appKey = response.data.data.bundleId;
                                self.bundleInfo = `获取的 bundleId 为 ${response.data.data.bundleId}`;
                            }
                            else {
                                self.bundleInfo = `请输入正确的 appKey`;
                            }
                        })
                        .catch(function(error) {
                            self.bundleInfo = `获取 bundleId 失败。`;
                        });
                }
            },
            /**
             * 参数拼接
             *
             * @param  {Object} params 源参数对象
             * @return {string} 拼接后的参数字符串
             */
            paramsJoin(params) {
                if (params instanceof Object) {
                    let result = [];
                    for (let key in params) {
                        result.push(key + '=' + params[key]);
                    }
                    return result.join('&');
                }
                return params;
            },
            getWebUrl() {
                const baseAppkey = this.model.appKey.split('_')[0];
                const appkey = baseAppkey.replace(/[A-Z]/g, function (text) {
                    return text.toLowerCase() + '-';
                });
                const baseUrl = [appkey, 'smartapps', 'cn'].join('.') + '/' + this.model.path;
                return window.location.protocol + '//' +  baseUrl
                    + (this.model.query ? '?' + this.model.query : '');
            },
            preview() {
                const url = this.getWebUrl();
                if (window.innerWidth < 420) {
                    window.location.assign(url);
                }
                else if (this.weburl === url){
                    this.weburl = '';
                    const that = this;
                    setTimeout(function () {
                        that.weburl = url;
                    }, 500);
                }
                else {
                    this.weburl = url;
                }
            }
        }
    }
</script>



